import React from "react";
import { useAuth } from "../contexts/AuthContext";

interface HeaderProps {
  protocolName?: string;
  lastUpdated?: Date;
}

const Header: React.FC<HeaderProps> = ({ protocolName, lastUpdated }) => {
  const { user, logout } = useAuth();

  const handleLogout = () => {
    if (
      window.confirm(
        "Are you sure you want to log out? Any unsaved changes will be lost.",
      )
    ) {
      logout();
    }
  };

  return (
    <header className="bg-white shadow-sm border-b flex-shrink-0">
      <div className="px-6 py-4">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-900">
              Delphi Protocol Builder
            </h1>
            <p className="text-gray-600">
              Design data-gathering protocols for dementia studies
            </p>
            {protocolName && (
              <p className="text-sm text-gray-500 mt-1">
                Current protocol:{" "}
                <span className="font-medium">{protocolName}</span>
              </p>
            )}
          </div>
          <div className="flex items-center space-x-4">
            <div className="text-right">
              {lastUpdated && (
                <div className="text-sm text-gray-500">
                  Last updated: {lastUpdated.toLocaleDateString()}
                </div>
              )}
              {user && (
                <div className="text-sm text-gray-600">User ID: {user.id}</div>
              )}
            </div>
            <button
              onClick={handleLogout}
              className="inline-flex items-center px-3 py-2 border border-gray-300 shadow-sm text-sm leading-4 font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            >
              <svg
                className="-ml-0.5 mr-2 h-4 w-4"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1"
                />
              </svg>
              Logout
            </button>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
